.container {
  width: 150px;
  height: 150px;
}
.img {
  margin-top: 15px;
  width: 100px;
  height: 100px;

  border: 6px solid #5f3ad7;
  border-radius: 50%;
}
.tittle {
  position: absolute;
  width: 165px;
  height: 30px;

  font-family: Revalia;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  /* identical to box height */
  align-self: center;

  color: #ffffff;
}
