.profile {
  width: 60px;
  height: 60px;
  border: 6px solid #5f3ad7;
  border-radius: 50%;
  z-index: 100;
}
.nameAndLogo {
  position: inherit;
  display: flex;
  flex-direction: row;
  align-items: baseline;
}
.name {
  position: absolute;
  width: 188px;
  height: 30px;
  margin-left: 20px;
    align-self: center;
  font-family: Revalia;
  font-style: normal;
  font-weight: normal;
  font-size: 24px;
  line-height: 30px;
  /* identical to box height */

  color: #ffffff;
}

.nameContainer{
    margin-left: -40px;
    padding-left: 40px;
    display: flex;
    justify-content: space-between;
width: 200px;
height: 43px;

z-index: 20;
background: #5E3BCE;
box-shadow: -8px 4px 52px -54px #6A51D0;
border-radius: 16px;
}
