body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.TextEditor {
  padding-top: 50px;
}

.TextEditor-title {
  text-align: center;
}

.TextEditor-title sup {
  font-size: 12px;
  font-weight: 400;
  position: absolute;
  color: #ababab;
  background-color: #e6e6e6;
  padding: 0 3px;
  border-radius: 3px;
  margin-left: 5px;
}

.TextEditor-editor {
  height: 650px;
  overflow: auto;
  
}

.TextEditor-tools {
  margin-top: 5px;
  margin-bottom: 50px !important;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}

.TextEditor-tools,
.TextEditor-editor,
.info {
  max-width: 593px;
  margin: 0 auto;
}

.info {
  padding-top: 35px;
  padding-bottom: 60px;
}


.click:hover{
  opacity: 0.5;
  cursor: pointer;
}